.label-shape {
	position: relative;
	width: 30px;
	height: 26px;
}

.label-shape::before {
	content: '';
	position: absolute;
	top: 0;
	right: -10px;
	width: 0;
	height: 0;
	border-top: 13px solid transparent;
	border-left: 10px solid var(--basic-2);
	border-bottom: 13px solid transparent;
	z-index: 1;
}

.arrow-pointer {
	width: 28px;
	height: 26px;
	position: relative;
	padding-left: 12px;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: 0;
		width: 0;
		height: 0;
		border-left: 10px solid var(--basic-1);
		border-top: 13px solid transparent;
		border-bottom: 13px solid transparent;
	}

	&:before {
		content: '';
		position: absolute;
		right: -13px;
		bottom: 0;
		width: 0;
		height: 0;
		border-top: 13px solid transparent;
		border-bottom: 13px solid transparent;
	}
}

$ber-colors: (
	'a': #56ae67,
	'b': #6eb865,
	'c': #b8d261,
	'd': #f1ec60,
	'e': #f2ba4e,
	'f': #e37d44,
	'g': #db433e,
);

//to dynamically create a class to update the style of ber tag based its value
@each $name, $color in $ber-colors {
	.ber-#{$name} {
		background-color: $color;

		&:before {
			border-left: 13px solid $color;
		}
	}
}
